@import 'template';
@import 'fonts';

/* header styles */

body{
	font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.navigation{
    background-color: $bgcolor-1;
    width: 100%;
    .content{
        border-top: 5px solid rgb(73, 69, 69);
    }
}

.content{
    max-width: $wd-site;
}

.main-content{
    margin-top: 15px;
    margin-left: 25px;
    margin-right: 25px;
    margin-bottom: 20px;
}

.nav-brand{
    .logo{
        display: block;
        width: 190px;
        padding: 10px;
        svg{
            padding: 0px;
        }
    }
}

/* Carousel */
.h-carousel{
    position: relative;
    max-width: 980px;
    .items{
        position: relative;
        .item::after{
            content: '';
            clear: both;
        }
    }
}

/* Menu */
#sidemenu{
    .menu{
        li{
            a{
                padding: 10px;
                font-family: Arial;
                text-transform: uppercase;
                border-bottom: 2px solid #FFF;
            }:hover{
                color: rgb(134, 134, 134);
                border-bottom: 2px solid rgb(134, 134, 134);
            }
        }
    }
}

.list-menu{
    list-style-type: none;
}

.btn-navbar{
    margin: 0 auto;
    background: #323232;
    color: #FFF;
    padding: 15px;
    border-radius: 5px;
    overflow: hidden;
    text-transform: uppercase;
    margin-right: 15px;
    svg{
        width: 15px;
        height: 15px;
        margin: 0px;
        padding: 0px;
        background: transparent;
        fill: #FFF;
    }
}

/* Box details */
.box-out{
    margin:10px;
}
.btn{
    cursor: pointer;   
}
.btn-general{
    padding: 10px;
    border-radius: 6%;
    &:hover{
        background-color: rgb(50, 50, 50);
        color:#FFF;
    }
}

.view{
    width: 100%;
    .cover{
        margin-top: 20px;
        margin-bottom: 20px;
        margin-left: auto;
        margin-right: auto;
        max-width: 450px;
        display: block;
    }
    .item{
        max-width: 800px;
        margin-left: auto;
        margin-right: auto;
        display: block;
    }
}

.page-item{
    margin: 20px;
    .btn-link{
        display: inline-block;
        padding: 2px;
        text-decoration: underline;
        color: rgb(4, 95, 28);
        font-weight: bolder;
    }
}

.modal-background{
    .modal-content{
        background-color: #FFF;
        max-width: 980px;
        display: block;
        margin-top: 25px;
        margin-left: auto;
        margin-right: auto;
        padding: 10px;
        position: relative;
        overflow: hidden;
    }
}

.item-title{
    background-color: #FFF;
    border-top: 1px solid #000;
    display: block;
    text-align: center;
    line-height: 25pt;
}

.option{
    position: relative;
    background-color: black;
    .spacing-1{
        padding: 10px;
    }
    .spacing-2{
        padding: 20px;
    }
    .spacing-3{
        padding: 30px;
    }
    .more{
        position: absolute;
        width: 100%;
        color:#FFF;
    }
    .bottom-up{
        bottom: 0px;
        background-color: rgba(0, 0, 0, 0.7);
    }
    .fade{
        top:0;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.3);
    }
}

.QMbmRe{
	width: 100%;
	height: 700px;
	display: block;
}

@media (min-width: 481px) and (max-width: 767px){
    
    #sidemenu{

        width: 200px;
        height: 100%;
        background-color: #FFF;
        position: fixed;
        top: 0px;
        left: 0px;
        z-index: 1;
        overflow-x: hidden;
        overflow-y: auto;
        border-left: 2px solid #9A9A9A;

        .nav-menu{
            margin: 20px;
            line-height: 20px;
            li{
                a{
                    font-size: 11pt;
                    border: none;
                }:hover{
                    border: none;
                }
            }
        }

        &.collapsed{
            left: -200px;
        }

    }

    .nav-side{
        .btn-collpase{
            display: block;
            visibility: visible;
        }
    }

    .row{
        col-3,col-4{
            width: 50%;
        }
    }

}

